import { CommonModule } from '@angular/common';
import { NgModule, ModuleWithProviders } from '@angular/core';
import { AbpModule } from '@abp/abp.module';
import { RouterModule } from '@angular/router';
import { NgxPaginationModule } from 'ngx-pagination';

import { AppAuthService } from './auth/app-auth.service';
import { AppRouteGuard } from './auth/auth-route-guard';
import { AbpPaginationControlsComponent } from './pagination/abp-pagination-controls.component';

import { HttpModule } from '@angular/http';
import { AuthService } from './auth/auth.service';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { TokenInterceptors } from '@shared/interceptors/token.interceptors'


@NgModule({
    imports: [
        CommonModule,
        AbpModule,
        RouterModule,
        NgxPaginationModule,
        HttpModule
    ],
    declarations: [
        AbpPaginationControlsComponent
    ],
    exports: [
        AbpPaginationControlsComponent
    ]
})
export class SharedModule {
    static forRoot(): ModuleWithProviders {
        return {
            ngModule: SharedModule,
            providers: [
                AppAuthService,
                AuthService,
                AppRouteGuard,
                { provide: HTTP_INTERCEPTORS, useClass: TokenInterceptors, multi: true }
            ]
        }
    }
}
