import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { BaseDto } from '@shared/models/base-dto';
import { Serializer } from '@shared/json/Serializer';
import { ServiceClientBase } from './service-client-base';
import { ApiResponse } from '@shared/models/api-response';

export abstract class ServiceCrudBase<T extends BaseDto> extends ServiceClientBase {
    constructor(
        httpClient: HttpClient,
        private endpoint: string,
        serializer: Serializer,
        baseUrl?: string
    ) {
        super(httpClient, serializer, baseUrl);
    }

    create(data: T): Observable<ApiResponse> {
        return this.post(data, this.endpoint);
    }

    update(data: T): Observable<ApiResponse> {
        return this.put(data, this.endpoint);
    }

    list(): Observable<ApiResponse> {
        return this.get<T[]>(this.endpoint);
    }

    deleteById(id: number): Observable<ApiResponse> {
        return this.delete(this.endpoint + '/' + id);
    }

    getById(id: number): Observable<ApiResponse> {
        return this.get<T>(this.endpoint + '/' + id);
    }

    // getSearch(searchDto: SearchBaseDto): Observable<ApiResponse> {
    //     return this.get<T[]>(
    //         this.endpoint + '/Search' +
    //         `?pagesize=${searchDto.pageSize}
    //         &pagenumber=${searchDto.pageNumber}
    //         &sort=${searchDto.sortType}${searchDto.sortName}`
    //         );
    // }

}
