import { Serializer } from '@shared/json/Serializer';
import { BaseSerializer } from '@shared/json/BaseSerializer';

export class ProfilePhotoDto {
    photo: string | undefined;
}

export class ProfilePhotoSerializer extends BaseSerializer implements Serializer {
    fromJson(json: any): ProfilePhotoDto {
        json = typeof json === 'object' ? json : {};
        const photoDto = new ProfilePhotoDto();
        photoDto.photo = json['photo'];
        return photoDto;
    }
    toJson(data: any) {
        // throw new Error("Method not implemented.");
    }
}
