import { NgModule } from '@angular/core';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { TokenInterceptors } from '@shared/interceptors/token.interceptors'

import { ProcessesServiceProxy } from './processes-service-proxy';
import { ProfilePhotoServiceProxy, UserServiceProxy } from './user-service-proxy';
import { UserService } from '@app/ekvt/services/trax/user.service';
import { SafetyCultureHistoryProxyService } from '@app/ekvt/services/safety-culture-history-proxy.service';
import { SafetyCultureProxyService } from '@app/ekvt/services/safety-culture-proxy.service';
import { SafetyCultureAttachmentService } from '@app/ekvt/services/safety-culture-attachment/safety-culture-attachment.service';

@NgModule({
    providers: [
        UserServiceProxy,
        SafetyCultureProxyService,
        SafetyCultureHistoryProxyService,
        SafetyCultureAttachmentService,
        UserService,
        ProcessesServiceProxy,
        ProfilePhotoServiceProxy,
        { provide: HTTP_INTERCEPTORS, useClass: TokenInterceptors, multi: true }
    ]
})
export class ServiceProxyModule { }
