import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Serializer } from '@shared/json/Serializer';
import { Observable, of } from 'rxjs';
import { AppConsts } from '@shared/AppConsts';
import { mergeMap } from 'rxjs/operators';
import { ApiResponse, ApiResponseSerializer } from '@shared/models/api-response';

export class ServiceClientBase {
    protected apiBaseUrl = AppConsts.apiBaseUrl;
    constructor(private httpClient: HttpClient,
        protected serializer: Serializer,
        baseUrl?: string
    ) {
        if (baseUrl) { this.apiBaseUrl = baseUrl; }
    }

    get<T>(endpoint: string): Observable<ApiResponse> {
        const uri = `${this.apiBaseUrl}/${endpoint}`;
        return this.httpClient
            .get(uri, { observe: 'response' })
            .pipe(mergeMap((response: Response) => {
                const apiResponse = ApiResponseSerializer.fromData(response.body);
                apiResponse.result = this.convertData<T>(apiResponse.result);
                return of(apiResponse);
            }));
    }

    post(data: any, endpoint: string): Observable<ApiResponse> {
        const uri = `${this.apiBaseUrl}/${endpoint}`;
        return this.httpClient
            .post(uri, this.serializer.toJson(data), { observe: 'response' })
            .pipe(mergeMap((response: Response) => {
                const apiResponse = ApiResponseSerializer.fromData(response.body);
                return of(apiResponse);
            }));
    }

    postWithFormData(data: FormData, endpoint: string) {
        const uri = `${this.apiBaseUrl}/${endpoint}`;

        return this.httpClient.post(uri, data, { observe: 'response' })
            .pipe(mergeMap((response: Response) => {
                const apiResponse = ApiResponseSerializer.fromData(response.body);

                return of(apiResponse);
            }));
    }

    postOfT<T>(data: any, endpoint: string): Observable<ApiResponse> {
        const uri = `${this.apiBaseUrl}/${endpoint}`;
        return this.httpClient
            .post(uri, this.serializer.toJson(data), { observe: 'response' })
            .pipe(mergeMap((response: Response) => {
                const apiResponse = ApiResponseSerializer.fromData(response.body);
                apiResponse.result = this.convertData<T>(apiResponse.result);
                return of(apiResponse);
            }));
    }

    postForSearchOfT<T>(data: any, endpoint: string): Observable<ApiResponse> {
        const uri = `${this.apiBaseUrl}/${endpoint}`;
        return this.httpClient
            .post(uri, this.serializer.toJsonForSearch(data), { observe: 'response' })
            .pipe(mergeMap((response: Response) => {
                const apiResponse = ApiResponseSerializer.fromData(response.body);
                apiResponse.result = this.convertData<T>(apiResponse.result);
                return of(apiResponse);
            }));
    }

    put(data: any, endpoint: string): Observable<ApiResponse> {
        const uri = `${this.apiBaseUrl}/${endpoint}`;
        return this.httpClient
            .put(uri, this.serializer.toJson(data), { observe: 'response' })
            .pipe(mergeMap((response: Response) => {
                const apiResponse = ApiResponseSerializer.fromData(response.body);
                return of(apiResponse);
            }));
    }

    putOfT<T>(data: any, endpoint: string): Observable<ApiResponse> {
        const uri = `${this.apiBaseUrl}/${endpoint}`;
        return this.httpClient
            .put(uri, this.serializer.toJson(data), { observe: 'response' })
            .pipe(mergeMap((response: Response) => {
                const apiResponse = ApiResponseSerializer.fromData(response.body);
                apiResponse.result = this.convertData<T>(apiResponse.result);
                return of(apiResponse);
            }));
    }

    delete(endpoint: string): Observable<ApiResponse> {
        const uri = `${this.apiBaseUrl}/${endpoint}`;
        return this.httpClient
            .delete(uri, { observe: 'response' })
            .pipe(mergeMap((response: Response) => {
                const apiResponse = ApiResponseSerializer.fromData(response.body);
                return of(apiResponse);
            }));
    }

    deleteOfT<T>(endpoint: string): Observable<ApiResponse> {
        const uri = `${this.apiBaseUrl}/${endpoint}`;
        return this.httpClient
            .delete(uri, { observe: 'response' })
            .pipe(mergeMap((response: Response) => {
                const apiResponse = ApiResponseSerializer.fromData(response.body);
                apiResponse.result = this.convertData<T>(apiResponse.result);
                return of(apiResponse);
            }));
    }

    private convertData<T>(data: any): any {
        if (Array.isArray(data)) {
            return data.map(item => this.serializer.fromJson(item) as unknown as T);
        } else {
            return this.serializer.fromJson(data) as unknown as T;
        }
    }
}
