import { Injectable, Injector, Inject } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { AuthService } from '@shared/auth/auth.service';
import { AppConsts } from '@shared/AppConsts';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { map, catchError } from 'rxjs/operators';
import { _throw } from 'rxjs/observable/throw';
import { NotifyService } from 'abp-ng2-module/dist/src/notify/notify.service';

@Injectable()
export class TokenInterceptors implements HttpInterceptor {
  constructor(private injector: Injector) { }
  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    const authService = this.injector.get(AuthService);
    const loaderService = this.injector.get(NgxUiLoaderService);
    const notifyService = this.injector.get(NotifyService);
    const isFormData = this.isFormData(request.body);

    const clonedRequest = request.clone({
      headers: authService.getTokenHeader(request, isFormData),
      url: this.fixUrl(request.url)
    });

    loaderService.start();

    return next.handle(clonedRequest)
      .pipe(
        map((event: HttpEvent<any>) => {
          if (event instanceof HttpResponse) {
            loaderService.stop();
            const response = event.body;
            if (response.StatusCode === 401) {
              authService.startSigninMainWindow();
            }
            if (typeof response.Error !== 'undefined' && response.Error !== null) {
              notifyService.error(response.Error.Message);
            }
          }
          return event;
        }),
        catchError((err: any) => {
          loaderService.stop();
          if (err instanceof HttpErrorResponse) {
            notifyService.error(err.message);
          }
          return of(err);
        }));
  }
  private fixUrl(url: string) {
    if (url.indexOf('http://') >= 0 || url.indexOf('https://') >= 0) {
      return url;
    } else {
      return AppConsts.apiBaseUrl + url;
    }
  }

  isFormData(toBeDetermined: any): boolean {
    const returnObject = toBeDetermined instanceof FormData

    return returnObject;
  }
}
