import { HttpClient } from '@angular/common/http';
import { ServiceClientBase } from './service-client-base';
import { ApiResponse } from '@shared/models/api-response';
import { UserPermissionsDto, UserPermissionsSerializer } from '@shared/models/user-permissions-dto';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ProfilePhotoDto, ProfilePhotoSerializer } from '@shared/models/profile-photo-dto';


@Injectable()
export class UserServiceProxy extends ServiceClientBase {
    constructor(httpClient: HttpClient) {
        super(
            httpClient,
            new UserPermissionsSerializer(),
        )
    }

    getPermissions(registerId: string): Observable<ApiResponse> {
        return this.get<UserPermissionsDto[]>(
            'ums/UserPermissions/' +
            registerId
        );
    }
}

@Injectable()
export class ProfilePhotoServiceProxy extends ServiceClientBase {
    constructor(httpClient: HttpClient) {
        super(
            httpClient,
            new ProfilePhotoSerializer()
        )
    }

    getProfilePhoto(registerId: string): Observable<ApiResponse> {
        return this.get<ProfilePhotoDto>(
            'commonapi/GetUserPhotoByRegisterId/' +
            registerId
        );
    }
}
