import { Serializer } from "@shared/json/Serializer";
import { BaseSerializer } from "@shared/json/BaseSerializer";

export class UserPermissionsDto {
    id: number | undefined;
    registerId: string | undefined;
    controller: string | undefined;
    action: string | undefined;
    displayName: string | undefined;
    projectId: number | undefined;
    projectName: string | undefined;
    state: string | undefined;
    url: string | undefined;
    name: string | undefined;
    templateUrl: string | undefined;
    angularController: string | undefined;
    angularAction: string | undefined;
    menuDirection: number | undefined;
    icon: string | undefined;
    area: string | undefined;
    isActive: number | undefined;
    isSticky: number | undefined;
    parentId: number | undefined;
    sortOrder: number | undefined;
}

export enum MenuDirection { Permission = 0, Menu = 1, LinkMenu = 2, State = 3, ParentMenu = 4 }

export class UserPermissionsSerializer extends BaseSerializer implements Serializer {
    fromJson(json: any): UserPermissionsDto {
        json = typeof json === 'object' ? json : {};
        const userPermissions = new UserPermissionsDto();
        userPermissions.id = json['ID']
        userPermissions.registerId = json['REGISTER_ID']
        userPermissions.controller = json['CONTROLLER']
        userPermissions.action = json['ACTION']
        userPermissions.displayName = json['DISPLAY_NAME']
        userPermissions.projectId = json['PROJECT_ID']
        userPermissions.projectName = json['PROJECT_NAME']
        userPermissions.state = json['STATE']
        userPermissions.url = json['URL']
        userPermissions.name = json['NAME']
        userPermissions.templateUrl = json['TEMPLATE_URL']
        userPermissions.angularController = json['ANGULAR_CONTROLLER']
        userPermissions.angularAction = json['ANGULAR_ACTION']
        userPermissions.menuDirection = json['MENU_DIRECTION']
        userPermissions.icon = json['ICON']
        userPermissions.area = json['AREA']
        userPermissions.isActive = json['IS_ACTIVE']
        userPermissions.isSticky = json['IS_STICKY']
        userPermissions.parentId = json['PARENT_ID']
        userPermissions.sortOrder = json['SORT_ORDER']
        return userPermissions;
    }
    toJson(resource: UserPermissionsDto) {
        const data = {};
        data['ID'] = resource.id;
        data['REGISTER_ID'] = resource.registerId;
        data['CONTROLLER'] = resource.controller;
        data['ACTION'] = resource.action;
        data['DISPLAY_NAME'] = resource.displayName;
        data['PROJECT_ID'] = resource.projectId;
        data['PROJECT_NAME'] = resource.projectName;
        data['STATE'] = resource.state;
        data['URL'] = resource.url;
        data['NAME'] = resource.name;
        data['TEMPLATE_URL'] = resource.templateUrl;
        data['ANGULAR_CONTROLLER'] = resource.angularController;
        data['ANGULAR_ACTION'] = resource.angularAction;
        data['MENU_DIRECTION'] = resource.menuDirection;
        data['ICON'] = resource.icon;
        data['AREA'] = resource.area;
        data['IS_ACTIVE'] = resource.isActive;
        data['IS_STICKY'] = resource.isSticky;
        data['PARENT_ID'] = resource.parentId;
        data['SORT_ORDER'] = resource.sortOrder;
        return data;
    }

}
