import { BaseDto } from './base-dto';
import { Serializer } from '@shared/json/Serializer';
import { Injectable } from '@angular/core';
import { BaseSerializer } from '@shared/json/BaseSerializer';

export class ProcessDto extends BaseDto {
    parentProcessCode: string | undefined;
    processCode: string | undefined;
    processName: string | undefined;
    supplier: string | undefined;
    input: string | undefined;
    output: string | undefined;
    customer: string | undefined;
}

@Injectable()
export class ProcessSerializer extends BaseSerializer implements Serializer {

    fromJson(json: any): ProcessDto {
        json = typeof json === 'object' ? json : {};
        const process = new ProcessDto();

        process.id = json['ID'];
        process.parentProcessCode = json['PARENT_PROCESS_CODE'];
        process.processCode = json['PROCESS_CODE'];
        process.processName = json['PROCESS_NAME'];
        process.supplier = json['SUPPLIER'];
        process.input = json['INPUT'];
        process.output = json['OUTPUT'];
        process.customer = json['CUSTOMER'];

        return process;
    }

    toJson(resource: ProcessDto) {
        const data = {};

        data['ID'] = resource.id;
        data['PARENT_PROCESS_CODE'] = resource.parentProcessCode;
        data['PROCESS_CODE'] = resource.processCode;
        data['PROCESS_NAME'] = resource.processName;
        data['SUPPLIER'] = resource.supplier;
        data['INPUT'] = resource.input;
        data['OUTPUT'] = resource.output;
        data['CUSTOMER'] = resource.customer;

        return data;
    }
}
