import { Injectable, Inject, EventEmitter, Injector } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { HttpClient, HttpHeaders, HttpResponse, HttpResponseBase, HttpHandler } from '@angular/common/http';
import { Observable, throwError as _observableThrow, from, of as _observableOf } from 'rxjs';
import { ajax, AjaxResponse } from 'rxjs/ajax';
import * as Rx from 'rxjs/Rx';
import { mergeMap as _observableMergeMap, catchError as _observableCatch } from 'rxjs/operators';
import { UserManager, User } from 'oidc-client';
import { AppConsts } from '@shared/AppConsts';
import { HttpRequest } from '@angular/common/http/src/request';
import { SearchModelDto } from '@shared/models/search-model-dto';
import { UserService } from '@app/ekvt/services/trax/user.service';

@Injectable()
export class AuthService {
  settings: any = {
    authority: AppConsts.client.authority,
    client_id: AppConsts.client.client_id,
    redirect_uri: AppConsts.client.redirect_uri,
    post_logout_redirect_uri: AppConsts.client.post_logout_redirect_uri,
    response_type: AppConsts.client.response_type,
    scope: AppConsts.client.scope,
    secret_key: AppConsts.client.secret_key,
    accessTokenExpiringNotificationTime: AppConsts.client.accessTokenExpiringNotificationTime,
    filterProtocolClaims: AppConsts.client.filterProtocolClaims,
    revokeAccessTokenOnSignout: AppConsts.client.revokeAccessTokenOnSignout,
    loadUserInfo: AppConsts.client.loadUserInfo
    // silent_redirect_uri: 'http://localhost:4200/assets/silent-renew.html',
    // automaticSilentRenew: true,
    // silentRequestTimeout:10000,
    // includeIdTokenInSilentRenew: true,
  };
  mgr: UserManager = new UserManager(this.settings);
  userLoadededEvent: EventEmitter<User> = new EventEmitter<User>();
  currentUser: User;
  loggedIn = false;
  private httpClient: HttpClient;
  authHeaders: Headers;

  constructor(
    private http: Http,
    userService: UserService,
    @Inject(HttpClient) httpClient: HttpClient
  ) {
    this.httpClient = httpClient;
    // var abc = await this.mgr.getUser();
    this.mgr.getUser()
      .then((user) => {
        if (user) {
          this.loggedIn = true;
          this.currentUser = user;
          this.userLoadededEvent.emit(user);

          const searchModels = [];
          const searchModel: SearchModelDto = new SearchModelDto();
          searchModel.value = user.profile.register_id + '';
          searchModel.operator = '=';
          searchModel.fieldName = 'RegisterId';
          searchModel.caseSensitive = false;
          searchModels.push(searchModel);

          userService.getUsersWithPost(searchModels).subscribe(data => {
            if (typeof data.result !== 'undefined' && data.result !== null) {
              localStorage.setItem('TraxUser', JSON.stringify({ user: data.result[0] }));
            }
          });

          localStorage.setItem('currentUser', JSON.stringify({ registerId: user.profile.register_id, token: user.access_token }));
        } else {
          this.loggedIn = false;
        }
      })
      .catch((err) => {
        this.loggedIn = false;
      });

    this.mgr.events.addUserLoaded((user) => {
      this.currentUser = user;
      this.loggedIn = !(user === undefined);
    });

    this.mgr.events.addUserUnloaded((e) => {
      this.loggedIn = false;
    });



    this.mgr.events.addAccessTokenExpiring(function () {
      // console.log('token expiring...');
    });

    this.mgr.events.addAccessTokenExpired(function () {
      // console.log('token expired...');
    });

    this.mgr.events.addSilentRenewError(function (err) {
      console.log('silent renew error...');
      console.log(err);
    });
  }

  getNewToken(): Observable<any> {
    return ajax.post(
      AppConsts.client.authority + '/connect/token',
      {
        client_id: AppConsts.client.client_id,
        client_secret: AppConsts.client.secret_key,
        scope: AppConsts.client.scopeForNewToken,
        grant_type: 'client_credentials'
      },
      {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    ).pipe(_observableMergeMap((response: any) => {
      if (response instanceof AjaxResponse) {
        if (response.status === 200) {
          return _observableOf(response.response);
        } else {
          return _observableThrow(response);
        }
      }
    }))
  }

  isLoggedInObs(): Observable<boolean> {
    return from(this.mgr.getUser()).pipe(_observableMergeMap((response_: any) => {
      if (response_) {
        return _observableOf(true);
      } else {
        return _observableOf(false);
      }
    }));
  }

  getShownLoginName(): string {
    return this.currentUser.profile.first_name + ' ' + this.currentUser.profile.surname;
  }

  clearState() {
    this.mgr.clearStaleState().then(function () {
      console.log('clearStateState success');
    }).catch(function (e) {
      console.log('clearStateState error', e.message);
    });
  }

  getUser() {
    this.mgr.getUser().then((user) => {
      this.currentUser = user;
      console.log('got user', user);
      this.userLoadededEvent.emit(user);
    }).catch(function (err) {
      console.log(err);
    });
  }

  removeUser() {
    this.mgr.removeUser().then(() => {
      this.userLoadededEvent.emit(null);
      console.log('user removed');
    }).catch(function (err) {
      console.log(err);
    });
  }

  startSigninMainWindow() {
    this.mgr.signinRedirect().then(function () {
      console.log('signinRedirect done');
    }).catch(function (err) {
      console.log(err);
    });
  }
  endSigninMainWindow(url) {
    this.mgr.signinRedirectCallback(url).then(function (user) {
      // location.href = AppConsts.appBaseUrl;
    }).catch(function (err) {
      console.log(err);
    });
  }
  endSigninMainWindowObs(url): Promise<User> {
    return this.mgr.signinRedirectCallback(url);
  }

  startSignoutMainWindow() {
    this.mgr.getUser().then(user => {
      return this.mgr.signoutRedirect({ id_token_hint: user.id_token }).then(resp => {
      }).catch(function (err) {
        console.log(err);
      });
    });
  };

  endSignoutMainWindow() {
    const as = this;
    this.mgr.signoutRedirectCallback().then(function (resp) {
      console.log('signed out', resp);
      as.startSigninMainWindow();
    }).catch(function (err) {
      console.log(err);
    });
  };

  /**
   * Example of how you can make auth request using angulars http methods.
   * @param options if options are not supplied the default content type is application/json
   */
  AuthGet(url: string, options?: RequestOptions): Observable<Response> {

    if (options) {
      options = this._setRequestOptions(options);
    } else {
      options = this._setRequestOptions();
    }
    return this.http.get(url, options);
  }
  /**
   * @param options if options are not supplied the default content type is application/json
   */
  AuthPut(url: string, data: any, options?: RequestOptions): Observable<Response> {

    let body = JSON.stringify(data);

    if (options) {
      options = this._setRequestOptions(options);
    }
    else {
      options = this._setRequestOptions();
    }
    return this.http.put(url, body, options);
  }
  /**
   * @param options if options are not supplied the default content type is application/json
   */
  AuthDelete(url: string, options?: RequestOptions): Observable<Response> {

    if (options) {
      options = this._setRequestOptions(options);
    }
    else {
      options = this._setRequestOptions();
    }
    return this.http.delete(url, options);
  }
  /**
   * @param options if options are not supplied the default content type is application/json
   */
  AuthPost(url: string, data: any, options?: RequestOptions): Observable<Response> {

    let body = JSON.stringify(data);

    if (options) {
      options = this._setRequestOptions(options);
    } else {
      options = this._setRequestOptions();
    }
    return this.http.post(url, body, options);
  }

  public getUser_(): Promise<User> {
    return this.mgr.getUser();
  }

  getTokenHeader(request: HttpRequest<any>, isFormData = false): HttpHeaders {
    if (this.currentUser.access_token) {

      if (isFormData) {
        const headers: HttpHeaders = new HttpHeaders();
        headers.append('Authorization', `Bearer ${this.currentUser.access_token}`);
        headers.append('Accept', 'application/json');
        headers.append('addTokenTo', 'header');

        return headers;
      }

      return new HttpHeaders({
        'Content-Type': 'application/vnd.api+json',
        Authorization: `Bearer ${this.currentUser.access_token}`,
        Accept: '*/*'
      });
    } else {
      return new HttpHeaders({
        'Content-Type': 'application/vnd.api+json',
        Accept: '*/*'
      });
    }
  }

  private _setAuthHeaders(user: any): void {
    this.authHeaders = new Headers();
    this.authHeaders.append('Authorization', user.token_type + ' ' + user.access_token);
    if (this.authHeaders.get('Content-Type')) {

    } else {
      this.authHeaders.append('Content-Type', 'application/json');
    }
  }
  private _setRequestOptions(options?: RequestOptions) {
    if (this.loggedIn) {
      this._setAuthHeaders(this.currentUser);
    }
    if (options) {
      options.headers.append(this.authHeaders.keys[0], this.authHeaders.values[0]);
    } else {
      options = new RequestOptions({ headers: this.authHeaders });
    }

    return options;
  }

}
