import { SearchModelDto } from '@shared/models/search-model-dto';

export class BaseSerializer {
    toJsonForSearch(resources: SearchModelDto[]) {
        const requestData = [];

        resources.forEach(resource => {
            const data = {};

            data['Operator'] = resource.operator;
            data['Value'] = resource.value;
            data['FieldName'] = resource.fieldName;
            data['CaseSensitive'] = resource.caseSensitive;

            requestData.push(data);
        });


        return requestData;
    }
}
